/* Home.css */

/* Container for the entire page */
.home {
    display: flex;
    flex-direction: row; 
    padding-left: 10px;
  }
  
  /* Left-side container for text content */
  .detel {
    flex: 1 1;
    padding: 20px; 
  }
  
  .detel h1 {
    font-size: 50px;
    color: #212121;
    margin-bottom: 20px;
    font-family: 'Montserrat', sans-serif;
  }
  
  .detel h1 span {
    color: red;
  }
  
  .detel p {
    line-height: 22px;
    font-family: 'Montserrat', sans-serif; 
  }
  
  /* Right-side container for the image */
  .images {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .images img {
    max-width: 100%;
    height: auto;
  }
  
  /* Container for work experiences */
  .experiences {
    
    padding: 20px;
    text-align: left; 
    font-family: 'Montserrat', sans-serif;
  }
  
  .experience {
    margin-top: 20px; 
    font-family: 'Montserrat', sans-serif;

  }
  
  .experience h3 {
    font-size: 20px;
    margin-bottom: 10px;
    color: red;
    text-align: left;  
    
  }
  
  .experiences span {
    font-size: 16px;
    color: #333;
    margin-bottom: 20px;
    display: block;
    text-align: left;
  }
  
  /* Media query for larger screens */
  @media only screen and (min-width: 768px) {
    .detel {
      margin-top: 5%;
    }
    .experiences {
      margin-top: 5%;
    }
  } 
  .exp_title{
    font-family: 'Montserrat', sans-serif;  
    text-decoration: underline; 
    margin-top: 7%;
    
  }
  
.menu {
  width: 100%;
  max-height: calc(100vh - 200px); /* Subtract footer height */
  overflow-y: auto; /* Enable vertical scrolling when content overflows */
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px; /* Added padding for spacing */ 
  font-family: 'Montserrat', sans-serif;
}

.menu .menuTitle {
  font-size: 36px; /* Reduced font size for responsiveness */
  margin-bottom: 20px; /* Added margin for spacing */ 
  font-family: 'Montserrat', sans-serif; 
  text-decoration: underline;

}

.menuList {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); /* Improved responsive grid */
  grid-gap: 20px;
  gap: 20px; /* Added gap between menu items */
  justify-items: center; /* Center items horizontally */
}

/* MENU ITEM STYLING */
.menuItem {
  border-radius: 15px;
  width: 100%;
  max-width: 300px; /* Limit maximum width for larger screens */
  height: auto;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  transition: 0.3s ease-in;
  cursor: pointer; /* Add cursor style for consistency */
}



.menuItem div {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  width: 100%;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.menuItem h1,
.menuItem p {
  margin: 10px; /* Adjusted margin for spacing */
}

/* Reset hyperlink styles */
.menuItem a {
  text-decoration: none; /* Remove underline */
  color: inherit; /* Inherit text color from parent element */
}

.menuItem a:hover {
  text-decoration: none; /* Remove underline on hover */
}

