/* Style for the "Go to Link" button */
.menuButton {
  border: none;
  border-radius: 20px; /* Adjust the border-radius to control the roundness */
  background-color: black; /* Button background color */
  color: gainsboro; /* Text color */
  padding: 10px 20px; /* Adjust padding as needed */
  cursor: pointer;
  font-family: 'Montserrat', sans-serif; /* Use the same font as menu items */
  font-size: 16px;
  transition: background-color 0.3s ease-in-out;
  text-decoration: none;
  display: inline-block;
  outline: none; /* Remove the button outline when clicked */
}

.menuButton:hover {
  background-color: red; /* Change color on hover */
}

